<template>
  <lay-header
    :showBack="true"
    :tBarColor="'#FFFFFF'"
    :isGetingItems="isGetingItems"
  >
    <v-layout column class="lay-body">
      <span class="txt-top">가입하셨던 <br />이메일 주소를 입력해주세요</span>
      <span class="txt-desc mt-4"
        >입력하신 이메일로 비밀번호 초기화 메일이 발송됩니다.</span
      >
      <c-input
        :hint="'이메일 주소를 입력해주세요'"
        :value.sync="inputList.id.value"
        class="mt-4"
      />
      <c-btn
        class="mt-5"
        :height="48"
        :thema="`default-bg${!inputList.id.value ? '-disable' : ''}`"
        :fontWeight="400"
        :fontSize="16"
        :readOnly="!inputList.id.value"
        :onClick="onClickActionNext"
        >다음</c-btn
      >
    </v-layout>
    <v-dialog persistent v-model="pop.isShow" :width="pxToRem(322)">
      <p-pass-send
        :onOk="
          () => {
            onClosePopupAction(pop);
            pushRouter({ name: 'login' });
          }
        "
        :email="inputList.id.value"
      />
    </v-dialog>
  </lay-header>
</template>

<script>
import pPassSend from "@/popup/pPassSend.vue";
export default {
  components: { pPassSend },
  middleware: "guest",
  data() {
    return {
      isGetingItems: false,
      inputList: {
        id: {
          value: null,
          errMsg: "",
          validation: () => {
            return this.checkId();
          },
        },
      },
      pop: {
        isCreate: false,
        isShow: false,
      },
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
  methods: {
    async onClickActionNext() {
      for (let key in this.inputList) {
        if (this.inputList[key].validation) {
          if (!this.inputList[key].validation()) {
            return false;
          }
        }
      }
      if (this.isGetingItems) {
        return;
      }
      this.isGetingItems = true;
      this.api({
        url: "/login/find-passwd",
        method: "post",
        data: {loginId: this.inputList.id.value},
        noPop: true,
      }).then(() => {
        this.onShowPopupAction(this.pop);
        this.isGetingItems = false;
      }).catch((err) => {
        if(err.code == '10005'){
          this.showPopup({
            title: err.msg,
            okTxt: "회원 가입하기",
            cancelTxt: "닫기",
            okAction: () => {
              this.pushRouter({ name: "join" });
            },
          });
        }else{
          this.onClosePopupAction(this.pop);
        }
        this.isGetingItems = false;
      });
    },
    checkId() {
      if (
        !this.inputList.id.value ||
        !this.validateEmail(this.inputList.id.value)
      ) {
        this.showToast("이메일 주소를 정확히 입력해주세요.");
        return false;
      }
      return true;
    },
  },
};
</script>

